
.navbar{ 
position: sticky;
top: 0;
left: 0;
bottom: 0;
right: 0;

    color: black;
    z-index: 9999;
   padding: 0px 0px;
   background-color: rgb(128, 0, 128);
}


#hs{
    background-color:rgba(0, 128, 0, 0.568);
}
#navbar-top {

    gap: 18px;
    margin-left: 20px;
  
}
#navbar-top li a{
    font-size: 20px;
    transition: all 1s ease-in-out;
    color: white;
}
#navbar-top li a::after{
    display: block;
    content: "";
    border: 1px solid white ;
    height: 1px;
    margin: auto;
    width: 0px;
    visibility: hidden;
    opacity: 0;
    transition: all 1s ease-in-out;

}
#navbar-top li a:hover::after{
    width: 100%;
    visibility: visible;
    opacity: 1;
} 
#navbar-top li a:hover{
    color: #FEA116;
    transform: translateY(-2px);
} 

#last-ic{
    align-items: center;
    gap: 19px;
}
#last-ic i{
    font-size: 24px;
    color: white;
}
#last-ic #search-button button{
    background-color: #FEA116;
    padding: 3px 12px;
    color: white;
}
#last-ic #search-button button:hover{
    background-color: #fea116bd;
    color: white;
}
/* profile start */
#hss{
    position: fixed;
    top: 80PX;
    right: 30%; 
    z-index: 99999999999;
    width: 40%;
    box-shadow: 5px 4px 8px rgba(0, 0, 0, 0.363);
    border-radius: 15px;
    display: none;
  }

  @media screen and (max-width : 560px) {
    #hss{
    position: absolute;
    top: 80PX;
    right: 0%; 
    width: 100%;
  }
  }

  @media (min-width:561px) and (max-width : 700px) {
    #hss{
    position: absolute;
    top: 80PX;
    right: 25%; 
    width: 50%;
  }
  }

  #cs{
     text-align: end;
     margin-right: 3%;
     
  }
  #cs i{
    font-size: 25px;
    margin-left: 3%;
  }
  
  #hss #hps{
    background-color: black;
    color: white;
    height: fit-content;
    width: 100%;
    text-align: center;
    border-radius: 15px;
  }

  #hps button{
    padding: 6px 8px;
    background-color: #FEA116;
    color: white;
    font-weight: bold;
    border: none;
    border-radius: 5px;
    margin-bottom: 10px;
  }
  #hps img{
    border-radius: 50%;
  }
