#filter-cate{
    display: flex;
    justify-content: end;
    align-items: center;
    margin-top: 12px;
    margin-right: 12px;
    gap: 7px;
}
#filter-cate input{
    padding: 8px 5px;
    border-radius: 15px;
    outline: none;
}
#filter-cate button{
    padding: 6px 18px;
    border-radius: 15px;
    background-color: #FEA116;
    color: white;
    
}

#shop-main{
    justify-content: center;
    align-items: center;
    margin-top: 10px;
}

.shop-item{
    text-align: center;
    border: 1px solid white;
    border-radius: 35px;
    background-color:rgba(128, 128, 128, 0.062) ;
    transition: all 0.3s ease-in-out;
    padding-bottom: 8px;
    margin-top: 3px;
}

.shop-item:hover{
    background-color: rgba(20, 20, 20, 0.089);
    border: 1px solid black;
    box-shadow: 0 0 15px 0 black;
}
.shop-item i{
    color: yellow
}