

#login-main{
  display: flex;
  justify-content: center;
  height: 100vh;
  background-color: blue;
  background-image: url(../../assets/backgroud-l-image.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  align-items: center;
  }
  #Login-form{
      width: 70%;
      background-color: rgba(131, 5, 5, 0.295);
      box-shadow: 4px 3px 5px black;
      border-radius: 5px;
      text-align: center;
      height: fit-content;
  }
  #Login-form h3{
      color: white;
      margin-top: 1%;
  }
  #Login-form h3::first-letter{
    color: rgb(60, 255, 0);
  }
  #Login-form input{
      width: 90%;
      margin: 12px 5%;
      padding: 10px 0px;
      outline: none;
      background-color: transparent;
       border: none;
       border-bottom: 2px solid grey;
       color: white;
       font-size: 20px;
  }
  #Login-form input::placeholder{
    color: white;
  }
  #Login-form button{
      padding: 1% 4%;
      text-align: center;
      margin: auto;
      background-color: black;
      font-weight: bold;
      border: none;
      border-radius: 5px;
      margin-bottom: 1%;  
      font-size: 18px;
      margin-left: 1%;
      color: white;
  }
  #Login-form button::first-letter{
    color: rgb(60, 255, 0);
  }
  #Login-form button:hover{
      background-color: rgba(0, 0, 0, 0.842);
  }
