#contity-main{
    display: flex;
    gap: 6px;
    
}
#contity-main #low{
    color: black;
    font-size: 25px;
}
#contity-main button{
    text-align: center;
    font-size: 20px;
    padding: 0px 10px;
    background-color: rgba(0, 0, 0, 0.637);
    color: white;
    border-radius: 5px;
    border: none;
    
}


#con-mainn{
    margin: 4px 0px;
    display: flex;
    gap: 10%;
}
#con-mainn .con-content{
    display: flex;
    gap: 10px;
}
#con-mainn .con-content del{
    font-size: 25px;
    color: gray;
}
#con-mainn  .con-content span{
    font-size: 25px;
    color:black;
}
#con-mainn .con-content p{
    font-size: 20px;
    color: gray;
    margin-top: 5px;
}