#Edit-main{
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url(../assets/backgroud-image.jpg);
    height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
 
}

#Edit-main .Edit-top{
    height: fit-content;
    width: 40%;
    padding: 15px 5%;
    box-shadow: 2px 2px 4px rgba(235, 157, 157, 0.363);
    color: white;
}
.Edit-heading{
    display: flex;
    justify-content: center;
    align-items: center;
}
.Edit-heading h4::first-letter{
    color: rgb(60, 255, 0);
}

.Edit-input input{
display: block;
width: 90%;
margin: 12px 5%;
height: 40px;
padding: 0px 10px;
border: none;
border-radius: 10px;
background-color: white;
outline: none;
}
.Edit-btn button{
    display: block;
    width: 90%;
    margin: 12px 5%;
    height: 40px;
    padding: 0px 10px;
    border: none;
    border-radius: 2%;
    font-size: 20px;
    background-color: black;
    color: white;
    transition: all 2s ease-in-out;
}
.Edit-btn button::first-letter{
    color: rgb(60, 255, 0)
}
.Edit-btn button:hover{
      scale: 1.03;
}

 /* ..............................0px-425px.......................... */

 @media screen and (max-width:430px){
    #Edit-main .Edit-top{
        width: 90%;
    }
 }

  /* ...............................430px-775px..........................*/
  @media (min-width:431px) and (max-width:775px) {
    #Edit-main .Edit-top{
        width: 90%;
    }
  }
