
h5{
    margin: 0px 0px;
}
p{
    margin: 0px 0px;
}
* {
    margin: 0;
    padding: 0;
}
.row {
    --bs-gutter-x: 1.5rem;
    --bs-gutter-y: 0;
    display: flex;
    flex-wrap: wrap;
    margin-top: calc(-1 * var(--bs-gutter-y));
    margin-right: calc(0 * var(--bs-gutter-x));
    margin-left: calc(0 * var(--bs-gutter-x));
}
#cate-top{
    text-align: center;
    padding: 12px 8px;
    transition: all 0.3s ease-in-out;
    border-radius: 35px;
    margin: 3px 0px;
    background-color: rgba(128, 128, 128, 0.068) ;
    /* transition: 0.3s; */
    border: 1px solid white;
}

#cate-top:hover{
    background-color: rgba(20, 20, 20, 0.089);
    border: 1px solid black;
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.61);
    /* scale: 1.01; */
    border-radius: 35px;

}

.details{
    display: flex;
    gap: 8px;
    font-size: 20px;
    flex-wrap: wrap;
}
.details a{
    text-decoration: none;
    color: black;
 
}
.details a li{
    list-style: none;
    margin-top: 12px;
}

.details a:hover{
   color: green;
  
}

#categorys{
    display: flex;
    justify-content: start;
    align-items: center;
}

#bars{
    display: none;
}
#x-bar{
    display: none;
}

#Ftt{
    justify-content: center;
}
#Ftt #fs1{
    background-image: url("./category-slider/banner-1\ .png");
    background-size: cover;
    background-repeat: no-repeat;
    background-color: gray;
    padding: 3% 3%;
    margin: 1px 1%;
    border-radius: 15px;
}
#fs1 button{
    margin-top: 16%;
    padding: 4px 9px;
    background-color: black;
    color: white;
    border-radius: 15px;
}
#fs1 button:hover{
   background-color: rgba(255, 68, 0, 0.822);
   color: white;
}

