@media screen and (max-width:460px) {
    #bars{
        display: block;
    }
    .details{
        display: none;
        gap: 0px;
        font-size: 17px;
        margin: 0px;
        padding: 0px;
    }
    .details a{
        
        transition: none;
    }
    #categorys{
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    
    #Ftt{
        padding: 0px  12px;
    }
    #Ftt #fs1{
