
#cr-top{
    display: flex;
    gap: 4%;
    margin: 5px 0px;
}
#cate-icons i{
   font-size: 20px;
   color: yellow;
}
#cate-icons span{
    font-size: 20px;
}
#cr-top button{
    border-radius: 10px;
    padding: 7px 12px;
    border: none;
    color: white;
    background-color: #FEA116;
    font-size: 20px;
}
#rout-slide-imgs .carousel-item{
    display: flex;
    justify-content: center;
    align-items: center;

}
#rout-slide-imgs .carousel-item img{
  
     width: 80%;
     margin: 1% 2%;
}

#rout-content p{
      font: bold;
      font-size:large;
}

#cr-top button:hover{
    background-color: #fea116d5;
    color: white;
}
#cr-slider-box{
background-color:white;
border-radius: 15px;
}

.row {
    --bs-gutter-x: 1.5rem;
    --bs-gutter-y: 0;
    display: flex;
    flex-wrap: wrap;
    margin-top: calc(-1 * var(--bs-gutter-y));
    margin-right: calc(0 * var(--bs-gutter-x));
    margin-left: calc(0 * var(--bs-gutter-x));
}

#cr-ralated{
    justify-content: center;
}