.row {
    --bs-gutter-x: 1.5rem;
    --bs-gutter-y: 0;
    display: flex;
    flex-wrap: wrap;
    margin-top: calc(-1 * var(--bs-gutter-y));
    margin-right: calc(0 * var(--bs-gutter-x));
    margin-left: calc(0 * var(--bs-gutter-x));
}
#p-head{
    margin: 1% 12px;
}
#p-main{
    margin: 0px 0px;
    padding: 0px 0px;
    justify-content: center;
    align-items: center;
    
}

#p-top{
    margin: 1% 0px;
    text-align: center;
    border: 1px solid white;
    border-radius: 35px;
    background-color:rgba(128, 128, 128, 0.068) ;
    transition: all 0.3s ease-in-out;
    height: fit-content;
}
#p-top:hover{
    background-color: rgba(20, 20, 20, 0.089);
    border: 1px solid black;
    box-shadow: 0 0 15px 0 black;
    border-radius: 35px;

}
#p-icon i{
    color: yellow
}

#p-add{
    width: 100%;
    text-align: center;
}
#p-add button{
    width: 85%;
    text-align: center;
    background-color: #FEA116;
    border-radius: 15px;
    margin: 5px 0px;
   color: black;
   border: none;
   font-size: 18px;
}
#p-add button:hover{
    background-color:#fea116d5;
}

