
#about-top{
    padding: 0px 0px;
    margin: 0px 0px;
}

#About-1{
    height: fit-content;
    min-height: 10vh;
}
.about-content{
    padding: 15px 10px;
    background-color: gainsboro;
}
.about-content h2{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 7px;
}
.about-content h2:after{
    content: "";
    border: 1px solid #FEA116;
    display: block;
    margin-top: 6px;
   height: 1px;
   width: 60px;
   background-color: #FEA116;    
}
.about-content h2::before{
    content: "";
    border: 1px solid #FEA116;
    display: block;
    margin-top: 6px;
   height: 1px;
   width: 60px;
   background-color: #FEA116;   
}
.about-content li{
    font-size: 20px;
    padding: 3px 12px;
    font-weight: 450;
    font: bold;
    font-style: oblique;

}